<template>
	<div class="tomika-admin-settings">
		<h1>Discord bot</h1>
		<div class="bot-info">
			<img :src="$store.state.discord.bot.avatarUrl">
			<div class="bot-main-info">
				<div class="bot-name">
					<span class="displayName">{{ $store.state.discord.bot.username }}</span>
					<span class="discriminator">#{{ $store.state.discord.bot.discriminator }}</span>
				</div>
				<div class="bot-status">
					<svg viewBox="-1 -1 2 2" :fill="$store.state.discord.bot.connected && !botStatusRequest ? 'rgba(67, 181, 129, 1)' : 'rgb(116, 127, 141)'">
						<circle r="1"></circle></svg>
					{{  $store.state.discord.bot.connected ? (botStatusRequest ? 'Disconnecting...' : 'Connected') : (botStatusRequest ? 'Connecting...' : 'Disconnected') }}
				</div>
			</div>
			<div class="bot-status-controls">
				<button :disabled="toggleBotStatusDisabled" @click="toggleBotStatus">
					<font-awesome-icon :icon="botStatusRequest ? 'spinner' : 'power-off'" :spin="botStatusRequest"></font-awesome-icon>
				</button>
			</div>
		</div>
		<tomika-block-message v-if="$store.state.discord.user.shouldUseGuildsScope" type="warning" small>You're connected to Discord without having authorised the guilds scope. This is normal for regular users, but relies on the Discord bot being connected to authorise you as admin. <a target="_blank" :href="`${$store.state.app.backEnd}/discord/connect?guilds=1`">Connect to discord using the guilds scope</a> and refresh the page to solve this and have access to toggling the bot's status.</tomika-block-message>
	</div>
</template>

<script>
	// Import dependencies
	import Vue from 'vue';
	import { library } from '@fortawesome/fontawesome-svg-core';
	import { faPowerOff, faSpinner } from '@fortawesome/free-solid-svg-icons';
	import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

	// Import components
	import tomikaBlockMessage from './tomika-block-message';

	// Font awesome
	library.add(faPowerOff, faSpinner);
	Vue.component('font-awesome-icon', FontAwesomeIcon);

	export default {
		name: "tomika-admin-settings",
		components: {
			tomikaBlockMessage
		},
		data() {
			return {
				botStatusRequest: false
			}
		},
		computed: {
			toggleBotStatusDisabled() {
				return this.botStatusRequest || (this.$store.state.discord.user && this.$store.state.discord.user.shouldUseGuildsScope);
			}
		},
		methods: {
			async toggleBotStatus() {
				this.botStatusRequest = true;
				const url = this.$store.state.discord.bot.connected ? `${this.$store.state.app.backEnd}/discord/bot/stop`
					: `${this.$store.state.app.backEnd}/discord/bot/start`;
				try {
					const toggleBotStatusResponse = await fetch(url, {
						method: 'POST',
						credentials: 'include'
					});
					if (toggleBotStatusResponse.ok) {
						this.getBotInfo();
					}
				} catch (err) {
					this.botStatusRequest = false;
				}
				this.botStatusRequest = false;
			},
			async getBotInfo() {
				const botInfoResponse = await fetch(`${this.$store.state.app.backEnd}/discord/bot/user`, {
					method: 'GET',
					credentials: 'include'
				});
				if (botInfoResponse.ok) {
					const botInfo = await botInfoResponse.json();
					this.$store.commit('discord/setBot', botInfo);
				}
			}
		}
	}
</script>

<style scoped>
	.tomika-admin-settings {
		padding: 10px 30px;
	}
	h1 {
		margin: 0 0 8px 0;
		font-size: 20px;
		padding: 0 8px;
		border-bottom: 1px solid hsl(0,0%,30%);
	}
	.bot-info {
		display: flex;
		flex-direction: row;
		align-items: center;
		padding: 0 20px;
		margin-bottom: 8px;
	}
	.bot-info + .tomika-block-message {
		margin-top: 16px;
	}
	.bot-info > img {
		width: 60px;
		border-radius: 100%;
		margin-right: 12px;
	}
	.bot-main-info {
		flex-grow: 1;
	}
	.bot-name {
		margin-bottom: 0.1em;
	}
	.displayName {
		font-size: 1.5em;
		margin-right: 0.1em;
	}
	.discriminator {
		color: hsl(0,0%,50%);
	}
	.bot-status {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	.bot-status > svg {
		width: 8px;
		margin-right: 6px;
	}
	.bot-status-controls button {
		font-size: 16px;
		width: 40px;
		height: 40px;
		padding: 0;
	}
</style>