export default {
	namespaced: true,
	state: {
		user: null,
		bot: null
	},
	getters: {
		connected: (state) => {
			return state.user === null ? false : state.user.connected;
		}
	},
	mutations: {
		setUser: (state, payload) => {
			if (typeof payload !== 'object' || typeof payload.connected !== 'boolean') { payload = null; }
			state.user = payload;
		},
		setBot: (state, payload) => {
			if (typeof payload !== 'object' || typeof payload.connected !== 'boolean') { payload = null; }
			state.bot = payload;
		}
	},
	actions: {

	}
}